.Logo_Maker_container {
  // background-color: skyblue;
  margin-top: 3em;
  background-color: #f7f7f7;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  .logo_Maker_heading {
    h1 {
      font-weight: bolder;
    }
  }
  .logo_maker_content {
    // background-color: yellow;
    width: 100%;
    height: 90%;
    display: flex;
    // flex-direction: column;
    justify-content: space-around;
    align-items: center;
    .content {
      // background-color: brown;
      width: 50%;
      height: 100%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      flex-direction: column;
      .text {
        // background-color: purple;
        margin-top: 2em;
        margin-left: 6em;
        width: 80%;
        height: 50%;
        display: flex;
        justify-content: center;
        // align-items: center;
        flex-direction: column;
        h2 {
          font-weight: 700;
        }
        p {
          // background-color: aqua;
          // padding-left: 45px;
          font-size: 15px;
        }
      }
      .Link {
        color: white;
        height: 10%;
        width: 30%;
        // background-color: red;
        .logo_maker_buttons {
          background-color: rgba(248, 0, 0, 0.123);
          // background-color: yellow;
          cursor: pointer;
          border-radius: 0.5em;
          height: 100%;
          width: 100%;

          display: flex;
          justify-content: center;
          align-items: center;
          color: rgb(247, 35, 35);
          a {
            font-weight: 700;
          }
          &:hover {
            color: #f7f7f7;
            background-color: rgb(247, 35, 35);
          }
        }
      }
    }
    .logo_maker_img {
      // background-color: green;
      overflow: hidden;
      overflow-x: hidden;
      position: relative;
      width: 50%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      // z-index: -100;
      img {
        position: absolute;
        margin-top: 3em;
        width: 80%;
        height: 80%;
        z-index: 10;
        left: 0;
        top: 0;
      }
    }
  }
}

.container_shape {
  // z-index: 9;
  position: absolute;
  right: -10%;
  top: 0;
  width: auto;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.triangles {
  margin-left: 12px;
  width: 18%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
}
.semi-circle {
  width: 230px;
  height: 110px;
  border-radius: 0 0 50% 50% / 0 0 100% 100%;
  border-bottom: 0;
  background-color: rgb(250, 179, 0);
  -webkit-box-reflect: below 1px
    linear-gradient(transparent, rgb(219, 191, 118));
}

.triangle {
  width: 0;
  height: 0;
  border-bottom: 100px solid rgb(50, 50, 228);
  border-right: 100px solid transparent;
}

.triangle2 {
  border-bottom: 130px solid rgb(50, 50, 228);
  border-right: 130px solid transparent;
}
.triangle4 {
  border-bottom: 130px solid rgb(50, 50, 228);
  border-right: 130px solid transparent;
}
.triangle3 {
  border-bottom: 100px solid rgb(112, 112, 255);
  border-right: 100px solid transparent;
}
.right_two {
  margin-left: -15px;
}

.triangle2 {
  border-bottom: 130px solid rgb(50, 50, 228);
  border-right: 130px solid transparent;
}
.triangle4 {
  border-bottom: 130px solid rgb(50, 50, 228);
  border-right: 130px solid transparent;
}
.triangle3 {
  border-bottom: 100px solid rgb(112, 112, 255);
  border-right: 100px solid transparent;
}
.right_two {
  margin-left: -15px;
}

@media (max-width: 991px) {
  .Logo_Maker_container {
    .logo_maker_content {
      flex-direction: column;
      .content {
        width: 100%;
        align-items: flex-start;
        // background-color: red;
        .text {
          margin-left: 2em;
          width: 100%;
        }
        .logo_maker_buttons {
          height: 15%;
          width: 30%;
          margin-left: 2em;
          margin-top: 0.5em;
        }
      }
      .logo_maker_img {
        width: 100%;
        // background-color: red;
        img {
          margin-left: 1em;
          margin-top: 0em;
          width: 90%;
          height: 100%;
        }
        .container_shape {
          top: -25%;
          right: -20%;
          transform: scale(0.6);
        }
      }
    }
  }
}
